import FadeIn from "react-fade-in/lib/FadeIn";
import { setLocalStorageItem } from "../utils/localstorage"
import { Button } from "./ui/Button";
import { supabase } from "../utils/Api";

export const AnswerQuestion = ({ question, user, onClick, handleAnswer }) => {

  const answerQuestion = async (aoId) => {
    try {
      if (user) {
        await storeAnswerFromExternalUser(aoId)
      } else {
        await storeAnswerFromAnonymous(aoId)
      }
      handleAnswer();
    } catch (e) {
      console.log(e);
    }
  }

  const storeAnswerFromAnonymous = async (aoId) => {
    const params = {
      input_answer_option_id: aoId, 
      input_feeling: 'UNKNOWN',
      input_question_id: question.id
    }
    return await supabase
      .rpc('store_answer_from_anonymous_user', params)
  }

  const storeAnswerFromExternalUser = async (aoId) => {
    if (user && question) {
      const params = {
        input_answer_option_id: aoId, 
        input_external_user_id: user.id, 
        input_question_id: question.id
      }
      return await supabase
        .rpc('store_answer_from_external_user', params)
    }
  }

  return <FadeIn>
    { question.answer_options.map(ao => <div key={ao.id}>
      <div style={{ marginTop: '4px' }}>
      <Button
        onClick={() => {
          answerQuestion(ao.id);
          setLocalStorageItem(question.id, ao.id);
          onClick(ao.id);
        }}
        text={ao.answer_title}
      >
      </Button>
      </div>
    </div>) }
  </FadeIn>
}